.ship__img-wrapper {
    width: 100%;
    height: 260px;
    margin-bottom: 10px;
}

.ship-img {
    width: 100%;
    height: 100%;
}

.btn-ship {
    color: #fff;
    opacity: 0;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -40%);
    border: none;
    border-radius: 10px;
    padding: 20px;
    text-transform: uppercase;
    transition: .4s;
    background: #ffffff;

    a {
        font-size: 25px;
    }
}

.ship__item-name {
    text-align: center;
}

@media screen and (max-width: 768px) {
    .ship__img-wrapper {
        height: 200px;
    }

    .ship__item-name {
        font-size: 20px;
    }
}

@media screen and (max-width: 520px) {
    .ship-list__item {
        padding: 5px;
    }

    .ship__img-wrapper {
        height: 120px;
        margin-bottom: 10px;
    }

    .ship__item-name {
        font-size: 17px;
    }
}

@media screen and (max-width: 380px) {
    .ship__img-wrapper {
        height: 100px;
    }
}

@keyframes animationbackground {
    0% {
        background-color: rgba(0, 0, 0, 0.2);
    }
    100% {
        background-color: rgba(20, 18, 18, 0.4);
    }
}